*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  font-family: @font;
}

ul {
  padding: 0 0 0 0;
  margin: 0 0 0 0;
}

li {
  list-style: none;
}

.page-body__scroll {
  overflow: hidden;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  max-width: 1340px;
  width: 100%;
  margin: 0 auto;
}

.page-main {
  background-color: @extra-gray;
}
